  height: 20px;
  width: 80px;
  margin-top: 5px;
  padding-bottom: 5px;
  color: white;
  font-size: 10px;
  z-index: 2;
}
.player.border{
  border-top: 0px;
  border-left: 0px;
  border-right: 0px;
  border-bottom: 1px;
  border-color: white;
  border-style: solid;
}
.active{
  width: 1px;
  background: white;
  height: 12px;
  margin-top: 3px;
  margin-left: 80px;
  position: absolute;

}
