.score{
  width : 29px;
  height: 18px;
  margin-top: 0px;
  margin-left: 50px;
  right :0;

  display: inline-block;
  z-index: 1;

  color: white;
  line-height: 20px;
  font-weight: bold;
  text-align: center;
}


.logo{
  position: absolute;
   top: 40%;
   left: 50%;
   width: 200px;
   height: 45px; /* Half the height */
   margin-left: -100px;
   margin-top: -33px;
   z-index: 1;
}
.smalllogo{
  position: absolute;
  right: 0;
  bottom: 0;
  margin-right: 40px;
  margin-bottom: 35px;
  width: 120px;
  height: 27px;
}

.progress{
  width: 200px;
  border-top: 0px;
  border-left: 1px;
  border-right: 1px;
  border-bottom: 0px;
  border-color: white;
  border-style: solid;
  display: flex;
  justify-content: center;
  align-items: center;


  flex-direction: column;
}
.progressbar{
  background: white;
  height: 1px;
}
.progresstext{
  color: white;
  font-size: 10px;
  margin-top: 5px;
  margin-bottom: 5px;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
